import {Component, ContentChild, Input, Inject, forwardRef} from '@angular/core';
import {SBItemBody} from './sb-item-body';
import {SqueezeBox} from './squeezebox';


export class SBItem {

    private squeezebox:SqueezeBox;

     public collapsed: boolean = true;
    
     body: SBItemBody;

    constructor( squeezebox: SqueezeBox) {
        this.squeezebox = squeezebox;
    }

    ngAfterViewInit() {
        this.body.toggle(this.collapsed);
    }
    toggle(collapsed: boolean) {
        this.squeezebox.didItemToggled(this);
        this.applyToggle(collapsed);
    }
    
    applyToggle(collapsed: boolean) {
        this.collapsed = collapsed;
        this.body.toggle(collapsed);
    }

static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
    exportAs: 'sbItem',
    selector: 'sb-item',
    template: `
        <div class="card" [ngClass]="{'is-collapsed': collapsed, 'active': !collapsed}">
            <ng-content></ng-content>
        </div>
    `
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: SqueezeBox, decorators: [{ type: Inject, args: [forwardRef(() => SqueezeBox), ] }, ]},
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'collapsed': [{ type: Input },],
'body': [{ type: ContentChild, args: [SBItemBody, ] },],
};
}
interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
